export default {
	props: {
		bodyStyle: String,
		
		// 头部标题
		headTitle: String,
		// 主体封面图
		bodyCover: String,
		// 是否显示头部
		showhead: {
			type: Boolean,
			default: true
		},
		// 标题是否加粗
		headTitleWeight:{
			type: Boolean,
			default: true
		},
		// 是否显示下边线
		headBorderBottom:{
			type: Boolean,
			default: true
		},
		// 是否给body加padding
		bodyPadding:{
			type: Boolean,
			default: false
		},
		
		cardStyle: {
			type: String,
			default: ""
		}
	},
	computed: {
		getHeadClass() {
			let BorderBottom = this.headBorderBottom ? "border-bottom" : ""
			return `${BorderBottom}`
		},
		getBodyClass() {
			let bodyPadding = this.bodyPadding ? "p-2" : ""
			return `${bodyPadding}`
		}
	}
}
